<template v-if="dataLoaded">
  <div id="spinpage">
    <table style="margin: auto;">
      <tr>
        <td @click="removeInput" style="padding-top: 160px;" class="list">
          <button class="button button-spin" @click="spin">Spin!</button>
          <button class="button button-add" @click="addOption">Add Option</button>
        </td>
        <td class="list">
          <Wheel v-if="segments.length > 0" :segNum="segNum" :segments="segments" :stopTime="(Math.random() * 7000) + 3000" ref="wheel" :colorList="colorList" @result="spinResult" @changeSelection="changeSelection"/>
        </td>
        <td>
          <OptionList v-if="segments.length > 0" class="option-list" :segments="segments" :segNum="segNum" :spinResult="result" ref="option" @changeOption="changeOptionText" @removeOption="removeOption" id="optionList" />
        </td>
      </tr>
    </table>

    <div class="info-box">Share this URL with other people to edit the options. This link will be active for at least 24 hours. {{url}}</div>
  </div>
</template>

<script>
import Wheel from './Wheel'
import OptionList from './OptionList'
import firebase from 'firebase'
import { newdb } from '../config';

export default {
  name: 'SpinPage',
  components: {
    Wheel,
    OptionList
  },
  props: [ 'db' ],
  computed: {
    segNum() {
      return this.segments.length;
    }
  },
  data() {
    return {
      pageId: this.newPage,
      dataLoaded: false,
      segments: [],
      colorList: ['lavenderblush', 'lightcoral', 'lightgreen', 'lightseagreen', 'lightsteelblue'],
      result: 9,
      buttonListId: 0,
      url: window.location.href
    }
  },
  watch: {
  },
  created() {
    this.pageId = this.$route.params['id'];
    var page = newdb.collection('shared_page').doc(this.pageId);
    var listData = this.segments;
    var colorList = this.colorList;
    page.get().then(function(doc) {
        if (doc.exists) {
          var data = doc.data();
          for (var i in data['options']) {
            listData.push({name: data['options'][i], rotation: '', optionId: parseInt(i)});
          }
        }
    })
    this.dataLoaded = true;
  },
  methods: {
    spin() {
      this.$refs.wheel.setTimer();
    },
    spinResult(value) {
      this.result = value;
    },
    changeSelection(id) {
      this.$refs.option.changeText(id);
    },
    changeOptionText(id, text) {
      this.segments[id]['name'] = text;
      var dbItemId = this.segments[id]['optionId'];
      var temp = this.db.collection('shared_page').doc(this.pageId);
      temp.get().then(function(doc) {
        if (doc.exists) {
          var data = doc.data();
          temp.update({
            ['options.' + dbItemId]: text
          })
        }
      })
    },
    removeInput(e) {
      this.$refs.option.removeTextInput();
    },
    addOption() {
      var temp = this.db.collection('shared_page').doc(this.pageId);
      var newId = 0;
      var dataList = this.segments;
      var curr = this;
      temp.get().then(function(doc) {
        if (doc.exists) {
          var data = doc.data();
          newId = data['nextId'];
          temp.update({
            ['nextId']: newId + 1,
            ['options.' + newId]: ''
          })

          dataList.push({ rotation: '', optionId: newId, name: ''});

          curr.$refs.option.addOption();
          curr.updateWheel();
        }
      })
      //this.segNum++;
    },
    removeOption(id) {
      //for (var i = id; i < this.segNum - 1; i++) {
      //  this.segments[i] = this.segments[i + 1];
      //}
      var listData = this.segments;
      if (this.pageId != '') {
        var page = this.db.collection('shared_page').doc(this.pageId);
        page.update({
          ['options.' + listData[id]['optionId']]: firebase.firestore.FieldValue.delete()
        })
      }
      this.segments.splice(id, 1);
      //this.segNum--;
      this.$nextTick(function () {
        this.$refs.wheel.draw();
      });
    },
    updateWheel() {
      this.$nextTick(function () {
        this.$refs.wheel.draw();
      });
    }
  }
}
</script>

<style scoped>
.button {
    background-color: white;
    color: black;
    outline: none;
    border: none;
    border-radius: 0px 25px 0px 25px;
    padding: 16px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
    width: 10rem;
    height: 5rem;
    display:block;
}

.button-spin { 
    border: 2px solid navy;
}

.button-spin:hover {
    background-color: navy;
    color: white;
}

.button-add {
    border: 2px solid purple;
}

.button-add:hover {
    background-color: purple;
    color:white;
}

.option-list {
    float: right;
}

.chosen::selection {
    color: yellow;
}

.info-box {
    width: 25rem;
    height: 4rem;
    padding-top: 1rem;
    margin: auto;
}

.list {
  vertical-align: top;
}
</style>



