<template v-if="dataLoaded">
  <div id="aboutpage">
    {{newPage}}
  </div>
</template>

<script>
import OptionList from './OptionList'

export default {
  name: 'AboutPage',
  components: {
    OptionList
  },
  props: [ 'newPage', 'db' ],
  beforeRouteLeave(to, from, next) {
    console.log('after')
    next()
  }
  
}
</script>

<style>
.button {
    background-color: white;
    color: black;
    outline: none;
    border: none;
    border-radius: 0px 25px 0px 25px;
    padding: 16px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
    width: 10rem;
    height: 5rem;
    display:block;
}

.button-spin { 
    border: 2px solid navy;
}

.button-spin:hover {
    background-color: navy;
    color: white;
}

.button-add {
    border: 2px solid purple;
}

.button-add:hover {
    background-color: purple;
    color:white;
}

.button-list {
    border: 2px solid crimson;
}

.button-list:hover {
    background-color: crimson;
    color: white;
}

.button-create {
    border: 2px solid maroon;
}

.button-create:hover {
    background-color: maroon;
    color:white;
}

.option-list {
    float: right;
}

.chosen::selection {
    color: yellow;
}

.list {
  vertical-align: top;
}
</style>



