<template v-if="dataLoaded">
  <div id="app">
    <table>
      <h1>Waffly</h1>
      <router-view :db="db" ref="clientRef"></router-view>
    </table>
  </div>
</template>

<script>
import firebase from 'firebase';
import { newdb } from './config';

export default {
  name: 'App',
  computed: {
    randomId() {
      for (var i = 0; i < 8; i++) {
        var random = parseInt(Math.random() * 3);
        this.linkId += random == 0 ? String.fromCharCode(Math.random() * 26 + 65) : random == 1 ? String.fromCharCode(Math.random() * 26 + 97) : parseInt(Math.random() * 10);
      }
      return this.linkId;
    }
  },
  data() {
    return {
      linkId: '',
      dataLoaded: false,
      db: {}
    }
  },
  firestore() {
  },
  mounted() {
    const firestore = firebase.firestore();
    const settings = {timestampsInSnapshots: true};
    firestore.settings(settings);
    this.db = firestore
  }
}
</script>

<style>
#app {
  font-family: Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

table {
  margin: auto;
}

h1 {
  margin: 2rem 0 0 0;
  color: #42b983;
  text-align: center;
}

</style>



