<template v-if="dataLoaded">
  <div id="defaultpage">
    <router-view></router-view>
    <table style="margin: auto;">
      <tr>
        <td @click="removeInput" style="padding-top: 80px;" class="list">
          <button class="button button-spin" @click="spin">Spin!</button>
          <button class="button button-list" @click="changeList">{{ buttonInfo }}</button>
          <button class="button button-add" @click="addOption">Add Option</button>

          <router-link :to="'custom/' + pageId"  style="text-decoration: none;"><button class="button button-create" @click="createPage"  v-if="this.$route.name == 'WafflyDefault'">Create a New Page to Share</button></router-link>
          
        </td>
        <td class="list">
          <Wheel :segNum="segNum" :segments="segments" :stopTime="(Math.random() * 7000) + 3000" ref="wheel" :colorList="colorList" @result="spinResult" @changeSelection="changeSelection"/>
        </td>
        <td>
          <OptionList class="option-list" :segments="segments" :segNum="segNum" :spinResult="result" ref="option" @changeOption="changeOptionText" @removeOption="removeOption" id="optionList" />
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import Wheel from './Wheel'
import OptionList from './OptionList'
//import { db } from '../main'
import firebase from 'firebase'
import { newdb } from '../config';

export default {
  name: 'SpinPage',
  components: {
    Wheel,
    OptionList
  },
  props: [ 'db' ],
  computed: {
    newPage() {
      var linkId = '';
      for (var i = 0; i < 8; i++) {
        var random = parseInt(Math.random() * 3);
        linkId += random == 0 ? String.fromCharCode(Math.random() * 26 + 65) : random == 1 ? String.fromCharCode(Math.random() * 26 + 97) : parseInt(Math.random() * 10);
      }
      return linkId;
    }
  },
  firestore() {
  },
  data() {
    return {
      loadedData: [],
      pageId: 0,
      dataLoaded: false,
      segNum: 10,
      segments: { 0: { rotation: '', color: 'lavenderblush', id: '', name: ''},
                  1: { rotation: '', color: 'lightcoral', id: '', name: ''},
                  2: { rotation: '', color: 'lightgreen', id: '', name: ''},
                  3: { rotation: '', color: 'lightseagreen', id: '', name: ''},
                  4: { rotation: '', color: 'lightsteelblue', id: '', name: ''},
                  5: { rotation: '', color: 'lavenderblush', id: '', name: ''},
                  6: { rotation: '', color: 'lightcoral', id: '', name: ''},
                  7: { rotation: '', color: 'lightgreen', id: '', name: ''},
                  8: { rotation: '', color: 'lightseagreen', id: '', name: ''},
                  9: { rotation: '', color: 'lightsteelblue', id: '', name: ''} 
                },
      result: 9,
      colorList: ['lavenderblush', 'lightcoral', 'lightgreen', 'lightseagreen', 'lightsteelblue'],
      dessertList: ['Ice Cream', 'Cake', 'Brownie', 'Pie', 'Cookie', 'Fruit', 'Chocolate', 'Cupcake'],
      buttonInfo: 'Decide on Dessert',
      buttonListId: 0
    }
  },
  created() {
    this.pageId = this.newPage
  },
  beforeRouteLeave(to, from, next) {
    next()
  },
  mounted() {
    this.init();
    this.dataLoaded = true;
  },
  methods: {
    init() {
      this.segments[0]['name'] = "Italian";
      this.segments[1]['name'] = "Chinese";
      this.segments[2]['name'] = "Mexican";
      this.segments[3]['name'] = "American";
      this.segments[4]['name'] = "Korean";
      this.segments[5]['name'] = "Indian";
      this.segments[6]['name'] = "Mediterranean";
      this.segments[7]['name'] = "Japanese";
      this.segments[8]['name'] = "French";
      this.segments[9]['name'] = "Seafood";
    },
    spin() {
      this.$refs.wheel.setTimer();
    },
    spinResult(value) {
      this.result = value;
    },
    changeSelection(id) {
      this.$refs.option.changeText(id);
    },
    changeOptionText(id, text) {
      this.segments[id]['name'] = text;
      var temp = this.db.collection('shared_page').doc(this.pageId);
      temp.get().then(function(doc) {
        if (doc.exists) {
          var data = doc.data();
          temp.update({
            ['options.' + id]: text
          })
        }
      })
    },
    removeInput(e) {
      this.$refs.option.removeTextInput();
    },
    addOption() {
      this.segments[this.segNum] = { rotation: '', color: this.colorList[this.segNum % this.colorList.length], optionId: this.segNum++, name: ''};
      this.$refs.option.addOption();
      this.updateWheel();
    },
    removeOption(id) {
      for (var i = id; i < this.segNum - 1; i++) {
        this.segments[i] = this.segments[i + 1];
      }
      if (this.pageId != '') {
        var page = this.db.collection('shared_page').doc(this.pageId);
        page.update({
          ['options.' + id]: firebase.firestore.FieldValue.delete()
        })
      }
      this.segNum--;
      this.$nextTick(function () {
        this.$refs.wheel.draw();
      });
    },
    changeList() {
      if (this.buttonListId == 0) {
        for (var i = 0; i < this.dessertList.length; i++) {
          this.segments[i]['name'] = this.dessertList[i];
        }
        this.segNum = this.dessertList.length;
        this.buttonInfo = 'Decide on Meal';
        this.buttonListId = 1;
      } else {
        this.init();        this.segNum = Object.keys(this.segments).length;
        this.buttonInfo = 'Decide on Dessert';
        this.buttonListId = 0;
      }
      this.updateWheel();
    },
    updateWheel() {
      this.$nextTick(function () {
        this.$refs.wheel.draw();
      });
    },
    createPage() {
      var createdAt = new Date();
      var hour = (createdAt.getHours() + 1) % 25;
      var options = {
        0: 'Italian',
        1: 'Chinese',
        2: 'Mexican',
        3: 'American',
        4: 'Korean',
        5: 'Indian',
        6: 'Mediterranean',
        7: 'Japanese',
        8: 'French',
        9: 'Seafood'
      };

      var temp = this.db.collection('shared_page_list').doc('temp_page');
      var uri = this.newPage;
      temp.get().then(function(doc) {
        if (doc.exists) {
          var data = doc.data();
          if (hour in data) {
            temp.update({
              [hour]: firebase.firestore.FieldValue.arrayUnion(uri)
            })
          } else {
            temp.update({
              [hour]: [uri]
            })
          }
        }
      })
      var nextId = 10;
      this.db.collection('shared_page').doc(this.newPage).set({
        options, nextId
      });
    }
  }
}
</script>

<style>
.button {
    background-color: white;
    color: black;
    outline: none;
    border: none;
    border-radius: 0px 25px 0px 25px;
    padding: 16px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
    width: 10rem;
    height: 5rem;
    display:block;
}

.button-spin { 
    border: 2px solid navy;
}

.button-spin:hover {
    background-color: navy;
    color: white;
}

.button-add {
    border: 2px solid purple;
}

.button-add:hover {
    background-color: purple;
    color:white;
}

.button-list {
    border: 2px solid crimson;
}

.button-list:hover {
    background-color: crimson;
    color: white;
}

.button-create {
    border: 2px solid maroon;
}

.button-create:hover {
    background-color: maroon;
    color:white;
}

.option-list {
    float: right;
}

.chosen::selection {
    color: yellow;
}

.list {
  vertical-align: top;
}
</style>



