import Vue from 'vue'
import Router from 'vue-router'
import App from '../App.vue'
import DefaultPage from '../components/DefaultPage.vue'
import SpinPage from '../components/SpinPage.vue'
import AboutPage from '../components/AboutPage.vue'


Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'WafflyDefault',
      /* DefaultPage used because beforeRouteLeave doesn't work with browser back button when component is the same */
      component: DefaultPage
    },
    {
      path: '/custom/:id',
      name: 'WafflySpin',
      component: SpinPage
    },
    {
      path: '/about',
      name: 'About',
      component: AboutPage
    }
  ]
})


